import React from 'react';
import PropTypes from 'prop-types';
import { useStaticQuery, graphql } from 'gatsby';

import Banner from 'components/ui/Banner';

const AboutIntro = () => {
  const { markdownRemark } = useStaticQuery(graphql`
    query {
      markdownRemark(frontmatter: { category: { eq: "intro section" } }) {
        frontmatter {
          title
          subtitle
          content
          linkTo
          linkText
        }
      }
    }
  `);

  const aboutIntro = markdownRemark.frontmatter;

  return (
    <Banner
      title={aboutIntro.title}
      subtitle={aboutIntro.subtitle}
      content={aboutIntro.content}
      linkTo={aboutIntro.linkTo}
      linkText={aboutIntro.linkText}
    />
  );
};

AboutIntro.propTypes = {
  title: PropTypes.string.isRequired,
  subtitle: PropTypes.string.isRequired,
  content: PropTypes.string.isRequired,
  linkTo: PropTypes.string.isRequired,
  linkText: PropTypes.string.isRequired
};

export default AboutIntro;
