import React from 'react';

import Container from 'components/ui/Container';
import { useStaticQuery, graphql } from 'gatsby';

import * as Styled from './styles';

const Footer = ({ children }) => {
  const { site, currentBuildDate } = useStaticQuery(graphql`
  query {
    site {
        siteMetadata {
          author
        }
    }
    currentBuildDate {
      currentDate
    }
  }
  `);

  const author = site.siteMetadata.author;
  const lastupdate = currentBuildDate.currentDate

  return (
    <Styled.Footer>
    <Container> <Styled.msg>{new Date().getFullYear()} &copy; {author} <br/>Built with Gatsby Personal Website · Hosted on Netlify · Last built on {lastupdate}</Styled.msg>
    </Container>
  </Styled.Footer>
  );
};


export default Footer;
